html {
    box-sizing: border-box;
    scroll-behavior: smooth;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    font-family: 'Montserrat' sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;

    background-color: #ffffff;
}

.noscroll {
    overflow: hidden;
}

.container {
    padding-left: 15px;
    padding-right: 15px;
    margin-right: auto;
    margin-left: auto;

    @media screen and (max-width: 479px) {
        padding-left: 20px;
        padding-right: 20px;
        width: 100%;
    }

    @media screen and (min-width: 480px) and (max-width: 767px) {
        padding-left: 20px;
        padding-right: 20px;
        width: 480px;
    }

    @media screen and (min-width: 768px) and (max-width: 1279px) {
        padding-left: 30px;
        padding-right: 30px;
        width: 768px;
    }

    @media screen and (min-width: 1280px) {
        width: 1280px;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

picture {
    margin: 0;
    padding: 0;
    display: block;
}

.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}

button {
    font-family: inherit;
    border: none;
    cursor: pointer;
    background-color: transparent;
}